.container {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: flex-start;

  padding: 15px;

  background-color: white;
  border-radius: 4px;

  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
